import React, { PropTypes } from 'react';
import { Link } from 'react-router';
import FabricSelector from './FabricSelector';
import { Wrapper, Block, Title2, Button3, Question, Center } from './styledComponents';

const Step3 = () => (
  <Wrapper>
    <Block>
      <Title2>
        Mes retouches
      </Title2>
      <Question>Afin de vous attribuer le couturier le plus adapté à vos besoins, si de préciser si votre commande
        comporte :</Question>
      <FabricSelector />
      <Center><Link to={'/order/step4'}><Button3>Valider</Button3></Link></Center>
    </Block>
  </Wrapper>
);

Step3.propTypes = {
};

export default Step3;



